@import url('https://fonts.googleapis.com/css?family=Russo+One');

.card {
 display: inline-block;
 border: white 3px solid;
 border-radius: 15px;
 -moz-border-radius: 20px;
 color: black;
 font-weight: bold;
 width: 280px;
 height: 415px;
 margin: 15px;
 padding: 10px;
 background-color: rgb(41, 41, 41);
 transition-duration: 0.3s;
 background-image: url('../../images/hallway.jpeg');
 background-repeat: no-repeat;
 background-size: 100% 108%;
 text-shadow:
 -1px -1px 0 #fff,
 1px -1px 0 #fff,
 -1px 1px 0 #fff,
 1px 1px 0 #fff;
 letter-spacing: 1px;
 overflow: hidden;

 &:hover {
   transform: scale(1.05);
   transition-duration: 0.3s;
   animation: glow .7s alternate linear infinite;
 }
}

h2 {
 margin-top: 30px;
}

.residents-p {
 display: inline-block;
 position: relative;
 top: -15px;
}

select {
 display: inline;
 position: relative;
 top: -15px;
 left: 5px;
}

.short-space {
 margin: 12px 0;
}

@keyframes glow {
 0%   {
   border-color: white;
 }
 25% {
   border-color: rgb(255, 255, 185);
   box-shadow: 0 0 1px 1px rgb(255, 255, 185);
   text-shadow:
   -1px -1px 0 rgb(255, 255, 185),
   1px -1px 0 rgb(255, 255, 185),
   -1px 1px 0 rgb(255, 255, 185),
   1px 1px 0 rgb(255, 255, 185);
 }
 50% {
   border-color: rgb(255, 255, 125);
   box-shadow: 0 0 2px 2px rgb(255, 255, 125);
   text-shadow:
   -1px -1px 0 rgb(255, 255, 125),
   1px -1px 0 rgb(255, 255, 125),
   -1px 1px 0 rgb(255, 255, 125),
   1px 1px 0 rgb(255, 255, 125);
 }
 75% {
   border-color: rgb(255, 255, 55);
   box-shadow: 0 0 3px 3px rgb(255, 255, 55);
   text-shadow:
   -1px -1px 0 rgb(255, 255, 55),
   1px -1px 0 rgb(255, 255, 55),
   -1px 1px 0 rgb(255, 255, 55),
   1px 1px 0 rgb(255, 255, 55);
 }
 100% {
   border-color: yellow;
   box-shadow: 0 0 4px 4px yellow;
   text-shadow:
   -1px -1px 0 yellow,
   1px -1px 0 yellow,
   -1px 1px 0 yellow,
   1px 1px 0 yellow;
 }
}

.active {
 background-color: yellow;
 color: black;
}

.residents-list {
 display: inline;
}

.image-div {
 width: 107%;
 height: 175px;
 position: relative;
 overflow: hidden;
 left: -10px;
 transition-duration: .3s;
 background-color: black;
 border-radius: 40px;
 margin-top: -30px;
}

.card:hover .image-div {
 height: 185px;
 transition-duration: 0.5s;
 border-radius: 0;
}

.card:hover .image {
 opacity: 1;
 transition-duration: 0.7s;
}

.image {
 opacity: 0.6;
 object-fit: cover;
 width: 100%;
 height: 100%;
 transition-duration: 0.5s;
}

.fav-btn {
 font-family: 'Russo One', arial, sans-serif;
 letter-spacing: 2px;
 font-size: 14px;
 font-weight: bold;
 width: 100%;
 background-color: white;
 color: black;
 border: black 1px solid;
 cursor: pointer;
 outline: none;
 border-radius: 10px;
 transition-duration: 0.3s;

 &:hover {
   transition-duration: 0.3s;
   background-color: #fff;
   border: 1px solid #b33ad5;
   background-color: #b33ad5;
   color: black;
 }
}

.active {
 color: #b33ad5;
 animation: buttonGlow 1.3s linear infinite;
 border: #ef91ff solid 1px;
 box-shadow: -1px -1px -1px solid pink;
}

.fav-btn-div {
width: 90%;
background-color: white;
border-radius: 10px;
margin: 0 auto;
display: flex;
bottom: 50px;
position: relative;
top: -80px;
justify-content: center;
align-items: baseline;
}

@keyframes buttonGlow {
 0% {
   box-shadow: 0 0 1px 4px #b33ad5;
   opacity: 1;
 }

 15% {
   box-shadow: 0 0 2px 5px #b33ad5;
   opacity: 0.8;
 }

 25% {
   box-shadow: 0 0 2px 4px #b33ad5;
   opacity: 0.9;
 }

 50% {
   box-shadow: 0 0 2px 5px #b33ad5;
   opacity: 0.8;
 }

 75% {
   box-shadow: 0 0 3px 4px #b33ad5;
   opacity: 0.9;
 }

 100% {
   box-shadow: 0 0 3px 4px #b33ad5;
   opacity: 1;
 }
}